import React, {useState} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid'
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
  } from "react-router-dom";

const useStyles = makeStyles(theme =>({
    heroContainer: {
    },
    heroTitle: {
        fontSize: '2rem',
        fontFamily: 'K2D, sans-serif',
        color: 'white',
    },
    heroText: {
        fontSize: '1.2rem',
        fontFamily: 'Open Sans, sans-serif',
        color: 'white'
    }
}))

function AveryHero(props) {

    const classes=useStyles()
    return (
        <Container maxWidth="sm" className={classes.heroContainer}>
            <Typography className={classes.heroTitle} align="center" color="textPrimary" gutterBottom>
                {props.primary}
            </Typography>
            <Typography className={classes.heroText} align="center" color="textSecondary" paragraph>
                {props.secondary}
            </Typography>
        </Container>
    )
}

export default AveryHero;